.menu-main {
  position: absolute;
  width: 360px;
  height: 100%;
  left: 0;
  top: 0;
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02);
  background-attachment: fixed;
  transform: translateX(-100%);
  z-index: 1000;
  background-color: white;
  opacity: 1;
  /* Изначально непрозрачный */ }

.menu-main.open {
  transform: translateX(0);
  opacity: 0.96;
  /* Делаем меню полупрозрачным */ }

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  /* Белый фон */
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  /* Чтобы клики проходили через псевдоэлемент */
  z-index: 999;
  /* Убедитесь, что оверлей находится под модальным окном */ }

.overlay.open {
  opacity: 0.96;
  /* Делаем оверлей полупрозрачным */
  pointer-events: auto;
  /* Восстанавливаем клики */ }

