:root {
  --color-primary: rgb(126, 126, 130);
  --color-secondary: rgb(27, 28, 33);
  --color-background: rgb(248, 248, 248);
  --color-border: rgb(217, 255, 245);
  --color-bullet: #b5b6bc;
  --color-highlight: rgb(65, 246, 215);
  --color-placeholder: rgb(181, 182, 188);
  --color-error: rgb(255, 62, 121);
  --color-white: rgb(255, 255, 255);
  --color-black: rgb(0, 0, 0); }

@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Regular.woff) format("sans-serif"); }

.block-four {
  margin-top: 50px; }

.block-four__header {
  background: linear-gradient(270deg, var(--color-background), var(--color-white) 98.614%);
  padding: 16px 16px 16px 0;
  margin-bottom: 24px;
  transform: rotate(180deg);
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid var(--color-border); }

@media (max-width: 767px) {
  .block-four__header {
    height: auto; } }

.block-four__header-text {
  color: var(--color-primary);
  font-family: 'TT Lakes', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-align: left;
  text-transform: uppercase;
  transform: rotate(180deg); }

#swiper2 {
  min-width: 320px;
  min-height: 200px;
  margin-top: 16px;
  margin-right: 0;
  margin-left: 16px; }

.block__item {
  display: flex;
  width: auto;
  align-items: center;
  width: 240px;
  height: 160px; }

.block__item img {
  width: 240px;
  height: 160px;
  border-radius: 16px; }

#swiper2 .swiper-pagination {
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  margin: 0 auto; }

.swiper-pagination-bullet {
  height: 12px;
  width: 12px;
  background: var(--color-bullet); }

.show-more-btn {
  display: none;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  align-items: center;
  margin-left: 16px;
  margin-top: 16px; }

.show-more-btn__icon {
  display: none;
  width: 12px;
  height: 12px;
  margin-right: 16px;
  margin-left: 16px; }

.show-more-btn__text {
  display: none;
  color: var(--color-secondary);
  font-family: 'TT Lakes', sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-align: left; }

@media (max-width: 767px) {
  #swiper2 .swiper-slide {
    display: flex; }
  #swiper2 .swiper-wrapper {
    display: flex; }
  .block-four {
    margin-top: 16px; } }

@media (min-width: 778px) and (max-width: 1119px) {
  .block-four__header {
    padding: 16px 24px 16px 0; }
  #swiper2 .swiper {
    margin-left: 0; }
  #swiper2 .swiper-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    -moz-column-gap: 23.25px;
         column-gap: 23.25px;
    row-gap: 16.83px; }
  .block__item {
    width: 224px;
    height: 160px; }
  .block__item:first-child {
    margin-left: 24px; }
  .block__item img {
    width: 224px;
    height: 160px; }
  #swiper2 .swiper-pagination {
    display: none; }
  .show-more-btn {
    display: flex; }
  .show-more-btn__icon {
    display: block; }
  .show-more-btn__text {
    display: inline-block; }
  .block__item:nth-child(n + 4) {
    display: none; } }

@media (min-width: 1120px) and (max-width: 1380px) {
  #swiper2 .swiper-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    -moz-column-gap: 16px;
         column-gap: 16px;
    row-gap: 16px;
    justify-content: center; } }

@media (min-width: 1381px) {
  #swiper2 .swiper-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    -moz-column-gap: 32px;
         column-gap: 32px;
    row-gap: 16px;
    justify-content: center; } }

@media (min-width: 1120px) {
  .block-four__header {
    padding: 16px 32px 16px 0; }
  #swiper2 .swiper {
    margin-left: 0; }
  .block__item img {
    width: 240px;
    height: 160px; }
  .show-more-btn {
    display: flex;
    margin-left: 32px;
    margin-top: 24px; }
  .show-more-btn__icon {
    display: block; }
  .show-more-btn__text {
    display: inline-block; }
  .swiper-pagination {
    display: none; }
  .block__item:nth-child(n + 5) {
    display: none; } }

@media (min-width: 1300px) {
  .block-four {
    margin-left: 306px; } }

@media (min-width: 1720px) {
  .block__item:nth-child(n + 5) {
    display: none; } }
