@charset "UTF-8";
:root {
  --color-primary: rgb(126, 126, 130);
  --color-secondary: rgb(27, 28, 33);
  --color-background: rgb(248, 248, 248);
  --color-border: rgb(217, 255, 245);
  --color-bullet: #b5b6bc;
  --color-highlight: rgb(65, 246, 215);
  --color-placeholder: rgb(181, 182, 188);
  --color-error: rgb(255, 62, 121);
  --color-white: rgb(255, 255, 255);
  --color-black: rgb(0, 0, 0); }

@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Regular.woff) format("sans-serif");
  src: url(./fonts/TTLakes-Medium.woff) format("sans-serif");
  src: url(./fonts/TTLakes-Bold.woff) format("sans-serif"); }

.block-three__header {
  width: 100%;
  margin-bottom: 24px;
  background: linear-gradient(270deg, var(--color-white), var(--color-background) 98.614%);
  height: 57px;
  border-top: 1px solid var(--color-border);
  margin-top: 50px;
  box-sizing: border-box; }

.block-three__header-text {
  color: var(--color-primary);
  font-family: 'TT Lakes-Regular', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: -0.2px;
  margin-left: 24px;
  text-align: left;
  text-transform: uppercase;
  padding-top: 16px; }

.table-head {
  background: var(--color-background);
  display: flex;
  flex-wrap: wrap;
  padding: 0 48px;
  height: 31px;
  align-items: center;
  width: calc(100% - 95px);
  margin-bottom: 16px; }

.table-head__text {
  flex: 1;
  margin-right: 20px;
  white-space: normal;
  word-wrap: break-word;
  vertical-align: middle;
  opacity: 0.5;
  font-family: 'TT Lakes-Regular', sans-serif; }

.table-head__price {
  width: 74px;
  text-align: start;
  opacity: 0.5;
  font-family: 'TT Lakes-Regular', sans-serif; }

.table-head__time {
  width: 81px;
  text-align: start;
  margin-right: 183px;
  color: var(--color-secondary);
  margin-left: 11px;
  opacity: 0.5;
  font-family: 'TT Lakes-Regular', sans-serif; }

.table-container {
  background: var(--color-white);
  max-width: calc(100% - 48px);
  margin-left: auto;
  margin-right: auto;
  border-radius: 6px; }

.table-container__box {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  justify-content: center;
  font-family: 'TT Lakes', sans-serif; }

.table-container__string {
  display: flex;
  flex-wrap: wrap;
  padding: 0 24px;
  box-sizing: border-box;
  height: 65px;
  align-items: center;
  width: 100%;
  font-family: 'TT Lakes', sans-serif; }

.table-container__box:first-child {
  border-top-left-radius: 6px;
  border-top-right-radius: 6px; }

.restangle {
  background: #eaeaea;
  width: calc(100% - 48px);
  height: 1px;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  position: relative; }

.table-container__service {
  flex: 1;
  margin-right: 20px;
  white-space: normal;
  word-wrap: break-word;
  vertical-align: middle;
  font-family: 'TT Lakes', sans-serif; }

.table-container__time {
  width: 81px;
  text-align: center;
  margin-right: 22px;
  color: var(--color-secondary);
  font-family: 'TT Lakes', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.2px;
  text-align: left;
  white-space: nowrap;
  vertical-align: middle; }

.table-container__price {
  width: 74px;
  margin-right: 10px;
  text-align: start;
  margin-left: auto;
  color: var(--color-secondary);
  font-family: 'TT Lakes', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.2px;
  text-align: left; }

.flex-item {
  flex: 1;
  text-align: end;
  padding: 10px;
  box-sizing: border-box;
  vertical-align: middle; }

.aside {
  width: 421px;
  margin-top: 24px;
  margin-left: 24px; }

.aside p {
  color: var(--color-secondary);
  font-family: 'TT Lakes', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0.2px;
  text-align: left; }

.table-container__link {
  position: relative;
  margin-left: 28px;
  vertical-align: middle; }

.offer-click {
  display: flex; }

.offer-click__link {
  margin-left: 24px;
  text-decoration: none;
  color: var(--color-secondary);
  font-family: 'TT Lakes', sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-align: left; }

@media (max-width: 380px) {
  .offer-click__link {
    max-width: 192px; } }

.offer-click__icon {
  position: relative;
  width: 24px;
  height: 24px;
  top: 7.5px; }

#swiper3 {
  display: none;
  border-radius: 6px;
  height: 240px;
  margin-left: 16px;
  overflow: hidden;
  position: relative;
  /* Добавлено для позиционирования пагинации */ }

#swiper3 .swiper-slide {
  display: flex;
  background: var(--color-white);
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 200px;
  max-width: 260px;
  border-radius: 6px;
  margin-right: 16px; }

#swiper3 .swiper-pagination {
  position: absolute;
  text-align: center;
  right: 0;
  margin-left: auto;
  top: 222px !important; }

.swiper-pagination-bullet {
  height: 12px;
  width: 12px;
  background: var(--color-bullet); }

#swiper3 .swiper-slide__box {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 16px;
  position: absolute; }

.group {
  margin-bottom: 16px; }

.group-first {
  margin-bottom: 16px; }

.group-second {
  margin-bottom: 16px; }

.group__name {
  color: var(--color-primary);
  font-family: 'TT Lakes', sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 0px;
  text-align: left;
  opacity: 0.72;
  margin-bottom: 4px; }

.group__value {
  color: var(--color-secondary);
  font-family: 'TT Lakes', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.2px;
  text-align: left;
  display: flex; }

.group__text {
  text-align: left; }

/* Медиа-запрос для разрешения до 460px */
@media (max-width: 760px) {
  .block-three__header {
    margin-top: 16px; }
  .table-head {
    display: none; }
  #swiper3 {
    display: flex; }
  .table-container {
    display: none; }
  .aside {
    box-sizing: border-box;
    width: 100%;
    padding: 0 16px 16px 16px;
    margin: 0; }
  .offer-click__link {
    margin-left: 16px; } }

@media (min-width: 1050px) {
  .block-three__header {
    margin-bottom: 31px; }
  .table-container {
    width: calc(100% - 64px); }
  .table-head {
    padding: 0 56px;
    width: calc(100% - 115px); }
  .table-head__time {
    margin-left: 0; }
  .table-container__price,
  .table-head__price {
    margin-right: 210px;
    margin-left: 0; }
  .block-three__header-text {
    margin-left: 32px; }
  .aside {
    margin-left: 32px;
    margin-top: 32px; }
  .offer-click__link {
    margin-left: 32px; } }

@media (min-width: 1300px) {
  .block-three {
    margin-left: 306px; } }
