:root {
  --color-primary: rgb(126, 126, 130);
  --color-secondary: rgb(27, 28, 33);
  --color-background: rgb(248, 248, 248);
  --color-border: rgb(217, 255, 245);
  --color-bullet: #b5b6bc;
  --color-highlight: rgb(65, 246, 215);
  --color-placeholder: rgb(181, 182, 188);
  --color-error: rgb(255, 62, 121);
  --color-white: rgb(255, 255, 255);
  --color-black: rgb(0, 0, 0); }

@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Regular.woff) format("sans-serif"); }

.header-two {
  width: 100%;
  margin-bottom: 24px;
  background: linear-gradient(270deg, var(--color-white), var(--color-background) 98.614%);
  height: 57px;
  border-top: 1px solid var(--color-border);
  margin-top: 50px; }

@media (max-width: 767px) {
  .header-two {
    height: auto;
    margin-top: 0; } }

.header-two__text {
  color: var(--color-primary);
  font-family: 'TT Lakes', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-align: left;
  padding: 16px; }

.block-two {
  margin-top: 32px; }

@media (max-width: 480px) {
  .block-two {
    margin-top: 16px; } }

@media (min-width: 1300px) {
  .block-two {
    margin-left: 306px; } }

#swiper1 {
  min-width: 320px;
  min-height: 110px;
  margin-top: 16px;
  margin-right: 0;
  margin-left: 16px; }

.block__brand {
  width: auto;
  align-items: center;
  height: 72px; }

.block__brand img {
  border-radius: 6px;
  width: 240px;
  height: 72px; }

#swiper1 .swiper-pagination {
  position: absolute;
  text-align: center; }

.swiper-pagination-bullet {
  height: 12px;
  width: 12px;
  background: var(--color-bullet); }

.more-btn {
  display: none;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  align-items: center;
  margin-left: 16px;
  margin-top: 16px; }

.more-btn__icon {
  display: none;
  margin-right: 6px;
  width: 12px;
  height: 12px; }

.more-btn__text {
  display: none;
  color: var(--color-secondary);
  font-family: 'TT Lakes', sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-align: left; }

@media (max-width: 767px) {
  #swiper1 {
    display: flex; }
  #swiper1 .swiper-wrapper {
    display: flex; } }

@media (min-width: 768px) and (max-width: 1119px) {
  #swiper1 {
    margin-left: 0; }
  #swiper1 .swiper-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    -moz-column-gap: 23.25px;
         column-gap: 23.25px;
    row-gap: 16.83px; }
  .block__brand {
    width: 217px;
    height: 75.75px; }
  .block__brand img {
    width: 217px;
    height: 75.75px; }
  .swiper-pagination {
    display: none; }
  .more-btn {
    display: flex;
    margin-left: 25.25px;
    margin-top: 25.25px; }
  .more-btn__icon {
    display: block; }
  .more-btn__text {
    display: inline-block; }
  .block__brand:nth-child(n + 7) {
    display: none; } }

@media (min-width: 1120px) and (max-width: 1380px) {
  #swiper1 .swiper-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    -moz-column-gap: 16px;
         column-gap: 16px;
    row-gap: 16px;
    justify-content: center; } }

@media (min-width: 1381px) {
  #swiper1 .swiper-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    -moz-column-gap: 32px;
         column-gap: 32px;
    row-gap: 16px;
    justify-content: center; } }

@media (min-width: 1120px) {
  #swiper1 {
    margin-left: 0; }
  .block__brand img {
    width: 240px;
    height: 72px; }
  .more-btn {
    display: flex;
    margin-left: 32px;
    margin-top: 24px; }
  .more-btn__icon {
    display: block; }
  .more-btn__text {
    display: inline-block; }
  .swiper-pagination {
    display: none; }
  .block__brand:nth-child(n + 9) {
    display: none; } }

@media (min-width: 1720px) {
  .block__brand:nth-child(n + 11) {
    display: none; } }
